import React from 'react';
import TechItems from './makeTechs/TechItems';

export default class extends React.Component {
    techContents = [
        {
            techLogo: './images/logos/html.png',
            techName: 'HTML',
            nameColor: '#e44d26',
            techDesc: ` ,  , data-* Attribute  HTML5     DOM API  .       <video>, <canvas>      .  <canvas> interactive             .`
        },
        {
            techLogo: './images/logos/css.png',
            techName: 'CSS',
            nameColor: '#379ad6',
            techDesc: `display, position, Flexbox, Gridbox  CSS   .   Gridbox    , animation, transition      . CSS     . SASS     .`
        },
        {
            techLogo: './images/logos/js.png',
            techName: 'JavaScript',
            techDesc: '     . Event loop, Hoisting     Event bubbling, debounce, throttle  Event Handling     . ES6        Babel  .      TypeScript  .'
        },
        {
            techLogo: './images/logos/react.png',
            techName: 'ReactJS',
            nameColor: '#0f6b9a',
            techDesc: ' ,     , React Hooks    . state  props      . React JS  OOP   . styled-components, react-router   .'
        },
        {
            techLogo: './images/logos/node.png',
            techName: 'NodeJS',
            nameColor: '#8bc74b',
            techDesc: 'Express.js    API     MVC    . MVC        .'
        },
        {
            techLogo: './images/logos/electron.png',
            techName: 'ElectronJS',
            nameColor: '#d5feff',
            techDesc: '     .       .     .'
        },
        {
            techLogo: './images/logos/python.png',
            techName: 'Python',
            nameColor: '#3571a3',
            techDesc: '  pygame            . Python   ,   Problem Solving  .  Level3      .'
        },
        {
            techLogo: './images/logos/mysql.png',
            techName: 'MySQL',
            nameColor: '#26557c',
            techDesc: '  Open API        . CRUD     .'
        },
        {
            techLogo: './images/logos/mobx.png',
            techName: 'mobX',
            nameColor: '#db5a14',
            techDesc: 'React JS     props state        .      ,         .'
        },
        {
            techLogo: './images/logos/aws.png',
            techName: 'AWS',
            nameColor: '#ff9900',
            techDesc: '     . EC2, RDS, Route53  SSH         .',
        },
    ];

    techIdx = -1;
    render() {
        return (
            <React.Fragment>
                {this.techContents.map(content => {
                    this.techIdx++
                    return (
                        <TechItems
                            key={this.techIdx}
                            techLogo={content.techLogo}
                            techName={content.techName}
                            nameColor={content.nameColor}
                            techDesc={content.techDesc}
                        />
                    )
                })}
            </React.Fragment>
        )
    }
};