import React from 'react';
import CraftItem from './makeCrafts/CraftItem';
import { inject, observer } from 'mobx-react';

@inject('yangStore')
@observer

class MakeCrafts extends React.Component {
    componentDidMount() {
        this.props.yangStore.getLastIdx(this.craftContents);
    };

    craftContents = [
        {
            imgUrl: './images/crafts/pd.png',
            craftTitle: 'PoongDung',
            githubUrl: 'https://github.com/taehwan920/poong-dung-react',
            craftUrl: 'http://poongdung.ga/',
            techs: [
                {
                    name: 'ReactJS',
                    color: '#61dafb'
                },
                {
                    name: 'CSS',
                    color: '#379ad6'
                },
                {
                    name: 'HTML',
                    color: '#e44d26'
                },
                {
                    name: 'NodeJS',
                    color: '#8cc84b'
                },
                {
                    name: 'Python',
                    color: '#3571a3'
                },
                {
                    name: 'MySQL',
                    color: '#26557c'
                },
                {
                    name: 'AWS',
                    color: '#ff9900'
                },
            ],
            desc: `     .     48   ,             .\n  4   3 .\n             .  IaaS AWS       .`
        },
        {
            imgUrl: './images/crafts/yt.png',
            craftTitle: 'YangTube',
            githubUrl: 'https://github.com/taehwan920/yangtube',
            craftUrl: 'https://5f33fbde2a723578f21bb406--boring-ardinghelli-8b5e2f.netlify.app',
            techs: [
                {
                    name: 'ReactJS',
                    color: '#0F6B9A'
                },
                {
                    name: 'Styled-component',
                    color: '#0F6B9A'
                },
                {
                    name: 'JavaScript',
                    color: '#000'
                },
                {
                    name: 'CSS',
                    color: '#379ad6'
                },
                {
                    name: 'HTML',
                    color: '#e44d26'
                },
                {
                    name: 'Netlify',
                    color: '#00C9B8'
                },
            ],
            desc: `     Front-End   ,        YouTube       YangTube . Front-End   Netlify .\n 7    .\n ,   ,  ,   ,          .      API , CSS    , OOP  .`
        },
        {
            imgUrl: './images/crafts/yth.png',
            craftTitle: 'YTH',
            githubUrl: 'https://github.com/taehwan920/YTH',
            craftUrl: '/',
            techs: [
                {
                    name: 'ReactJS',
                    color: '#0F6B9A'
                },
                {
                    name: 'Styled-component',
                    color: '#0F6B9A'
                },
                {
                    name: 'NodeJS',
                    color: '#8cc84b'
                },
                {
                    name: 'MobX',
                    color: '#db5a14'
                },
                {
                    name: 'CSS',
                    color: '#379ad6'
                },
                {
                    name: 'HTML',
                    color: '#e44d26'
                },
            ],
            desc: `   . React JS  Heroku .\n 8   2 .\nYangTube     props / state     React JS  .       MobX  .`
        },
    ];

    craftIdx = 0;

    render() {
        return (
            <React.Fragment>
                {this.craftContents.map(content => {
                    this.craftIdx++
                    return (
                        <CraftItem
                            key={this.craftIdx}
                            craftIdx={this.craftIdx}
                            content={content}
                        />
                    )
                })}
            </React.Fragment>
        )
    }
};

export default MakeCrafts;